<template>
   <div class="shopDiv">
     <van-tabs v-model="activeTabs" @change="changes" color="#FF8D59" title-inactive-color="#484848" title-active-color="#FF8D59" swipeable>
       <van-tab  title="我的门店">
         <shopLeft v-if="shopShow"></shopLeft>
       </van-tab>
       <van-tab title="下级代理商" >
         <shopRight v-if="!shopShow"></shopRight>
       </van-tab>
     </van-tabs>
   </div>
</template>

<script>
  const shopLeft = () => import("../../dataStat/shop/shopLeft.vue");
  const shopRight = () => import("../../dataStat/shop/shopRight.vue");

  export default {
    components:{
      shopLeft,
      shopRight
    },
      data() {
        return {
          activeTabs: 0,
          shopShow:true
        }
      },
      methods:{
        changes(index) {
          if (index == 1) {
            this.shopShow = false;
          } else {
            this.shopShow = true;
          }
        }
        },
    }
</script>

<style scoped>

</style>



